body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@font-face {
  font-family: "Regular";
  src: url(/static/media/UniNeueRegular.7e4e5ddb.otf);
  /* Safari, Android, iOS */ }

@font-face {
  font-family: "Bold";
  src: url(/static/media/UniNeueBold.7b99adec.otf);
  /* Safari, Android, iOS */ }

@font-face {
  font-family: "Heavy";
  src: url(/static/media/UniNeueHeavy.270b7d3f.otf);
  /* Safari, Android, iOS */ }

.regular {
  font-family: "Regular"; }

.bold {
  font-family: "Bold"; }

.heavy {
  font-family: "Heavy"; }

.navlink {
  text-decoration: none;
  margin: 24px;
  color: #4ba2cc;
  font-size: 1.125rem; }

.navlink:hover {
  color: #20578d; }

.navlink.selected {
  color: #20578d; }

.App {
  max-width: 1224px;
  padding: 0 24px 0 24px;
  position: relative;
  margin: auto; }

p {
  font-family: "Regular";
  color: #4ba2cc; }

.rightsideNavigation {
  display: -webkit-flex;
  display: flex;
  position: absolute;
  padding: 24px;
  top: 50%;
  right: 0px;
  -webkit-flex-direction: column;
          flex-direction: column; }

.rightsideNavigation .item {
  margin: 12px 0 12px 0;
  width: 8px;
  height: 8px;
  border-radius: 45px;
  background-color: #4ba2cc; }

.section {
  margin-top: 64px; }

.backgroundDecoration {
  background-color: #F5FAFD;
  height: 176px;
  width: 87%;
  border-top-right-radius: 90px;
  border-bottom-right-radius: 90px;
  position: absolute;
  left: -50%; }

.giveawayContainer {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: flex-start;
          align-items: flex-start; }

.giveawayHeading {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  font-size: 5rem;
  letter-spacing: 20px;
  margin-bottom: 96px;
  font-family: "Heavy";
  color: #4BA2CC; }

.giveawayLeft {
  display: inline-block; }

.giveawayRight {
  position: relative;
  top: -128px; }

.button.dark {
  background-color: #20578d; }

.button.light {
  background-color: #4ba2cc; }

.button.root {
  border-radius: 45px;
  height: 48px;
  width: 178px;
  font: 0.875rem "Heavy";
  color: white;
  border: 0;
  cursor: pointer; }

.button:focus {
  outline: 0; }

.button.dark:active {
  -webkit-transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
  transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
  background-color: #2971b7 !important; }

.button.light:active {
  -webkit-transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
  transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
  background-color: #73b7d7 !important; }

.button.dark:hover {
  -webkit-transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
  transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
  background-color: #173d63; }

.button.light:hover {
  -webkit-transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
  transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
  background-color: #3288b2; }

