$primaryColorMain: #4ba2cc;
$primaryColorDark: #20578d;
$primaryColorLight: #f5fafd;

@font-face {
  font-family: "Regular";
  src: url("./assets/fonts/UniNeueRegular.otf"); /* Safari, Android, iOS */
}

@font-face {
  font-family: "Bold";
  src: url("./assets/fonts/UniNeueBold.otf"); /* Safari, Android, iOS */
}

@font-face {
  font-family: "Heavy";
  src: url("./assets/fonts/UniNeueHeavy.otf"); /* Safari, Android, iOS */
}

.regular {
  font-family: "Regular";
}

.bold {
  font-family: "Bold";
}
.heavy {
  font-family: "Heavy";
}

.navlink {
  text-decoration: none;
  margin: 24px;
  color: $primaryColorMain;
  font-size: 1.125rem;
}

.navlink:hover {
  color: $primaryColorDark;
}

.navlink.selected {
  color: $primaryColorDark;
}

.App {
  max-width: 1224px;
  //width: 100%;
  padding: 0 24px 0 24px;
  position: relative;
  margin: auto;
}

p {
  font-family: "Regular";
  color: $primaryColorMain;
}

.rightsideNavigation {
  display: flex;
  position: absolute;
  padding: 24px;
  top: 50%;
  right: 0px;
  flex-direction: column;
}

.rightsideNavigation .item {
  margin: 12px 0 12px 0;
  width: 8px;
  height: 8px;
  border-radius: 45px;
  background-color: $primaryColorMain;
}
.section {
  margin-top: 64px;
}
